  /* LoadingSpinner.module.css? */
  .Card_spinner__1JW9h {
    display: inline-block;
    width: 80px;
    height: 80px;
  }
  .Card_spinner__1JW9h:after {
    content: ' ';
    display: block;
    width: 64px;
    height: 64px;
    margin: 8px;
    border-radius: 50%;
    border: 6px solid teal;
    border-color: teal transparent teal transparent;
    -webkit-animation: Card_spinner__1JW9h 1.2s linear infinite;
            animation: Card_spinner__1JW9h 1.2s linear infinite;
  }
  @-webkit-keyframes Card_spinner__1JW9h {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
  @keyframes Card_spinner__1JW9h {
    0% {
      -webkit-transform: rotate(0deg);
              transform: rotate(0deg);
    }
    100% {
      -webkit-transform: rotate(360deg);
              transform: rotate(360deg);
    }
  }
